<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container mb-4" v-if="is_response">
<!--      <div class="title_ans">-->
<!--        Тест на темперамент-->
<!--      </div>-->
      <div class="ans_card">
        <div class="row">
          <div class="col-xl-8">

            <div class="ans_title">{{an_res.title}}</div>
            <div class="ans_desc">
              {{an_res.description}}
            </div>

            <div class="link_group">
              <div>
                <a @click="getOnetest()" class="link_main decoration" href="#">
                  <div>
                    Testga qaytish
                  </div>
                </a>
              </div>
              <div>
                <router-link class="link_main_calk decoration" to="/start-test">
                  <div>
                    Boshqa testga kirish
                  </div>
                </router-link>
              </div>
            </div>
          </div>
          <div class="col-xl-4">
            <img :src="an_res.image_url" width="100%" class="ans_img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-if="!is_response">

      <div class="on_test_title">{{$store.state.testsId.title}}</div>
      <div class="on-test">
        <div class="one_test_progress">
          <div class="on-test-count">
             {{ activeIndex+1 }}/{{ $store.state.test_one.length }}
          </div>
          <div class="on-test-range">
            <range-slider
                class="slider"
                min="0"
                :max="$store.state.test_one.length"
                step="1"
                v-model="activeIndex">
            </range-slider>
          </div>
          <div class="on-test-card" v-for="(test, i) in  $store.state.test_one" :key="test.id">
            <div v-show="activeIndex == i">
              <div>
                <div class="on-test-quiz">
                  {{ test.title }}
                </div>
                <div class="answers ">
                  <label   v-for="option in test.choices"
                           :key="option.id"
                           class="ans_id container_input"
                           :for="`option${option.id}${test.id}`"
                  >
                    <input   v-model="answers[i]"
                             :value="option"
                             class=""
                             type="radio"
                             :id="`option${option.id}${test.id}`"
                    >
                    <span class="checkmark"></span>
<!--                    <div class="test_circle">-->
<!--                      <div class="test_circle_child"></div>-->
<!--                    </div>-->
                    <span class="ans_text">{{ option.text }}</span>
                  </label>
                </div>
              </div>
              <div class="on-test-button">
                <button class="prev" @click="prevStart()">

                  <span>Odldingisi</span>

                </button>
                <button v-if="activeIndex==($store.state.test_one.length-1)" class="next" :class="{'actives':answers.length>activeIndex}" @click="endTest()">
                  <span>Tugatish</span>
                </button>
                <button v-else class="next" :class="{'actives':answers.length>activeIndex}"  @click="nextStart(test,i)">
                  <span>Keyngisi</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  components: {Footer, Header, RangeSlider},
  data() {
    return {
      sliderValue: 1,
      value: 0,
      activeIndex: 0,
      max: 12,
      isActive: false,
      userResponses: [],
      answer: [],
      activeAn:[],
      an_res:{},
      is_response:false,
      option:'',
      answers:[],
    }
  },
  mounted() {
    this.$store.dispatch('getQuizOne', this.$route.params.id)
    this.max = this.$store.state.test_one.length
    this.$store.dispatch('getTestsId',this.$route.params.id)

  },
  methods: {
    getAns(item) {
      this.answer[this.sliderValue - 1] = {
        "question_id": this.$store.state.test_one[this.sliderValue - 1].id,
        "choice_id": item.id
      }

    },
    nextStart(test,i) {
      this.answer[this.activeIndex] = {
        "question_id":test.id,
        "choice_id": this.answers[i].id
      }
      if (this.activeIndex < this.$store.state.test_one.length) {
        this.activeIndex++
      }
      console.log(this.answer,"test")

    },
    endTest() {
      this.$axiosDefault.post(`test-result/`,
          {
            "answers": this.answer,
            "test_id": this.$route.params.id
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
      .then(res=>{
        this.an_res=res.data.result
        this.is_response=true
      })
    },
    getOnetest(){
      this.is_response=false
      this.activeIndex=0
      this.answer=[]
      this.answers=[]

    },
    prevStart() {
      if (this.activeIndex > 0) {
        this.activeIndex--
      }
    }
  }
}
</script>
